import React, { Component} from 'react';
import{} from 'react-bootstrap';
import Socialmedia from './socialmedia';
import Navigater from './navigater';
import './home.css';
import './animate.css';

class Home extends Component {
  render(){
    return(
      <div>
        <Navigater />
        <div className='hey' style={{background:'url(https://images.unsplash.com/photo-1481007553706-bde1ba8e91fd?ixlib=rb-0.3.5&q=85&fm=jpg&crop=entropy&cs=srgb&ixid=eyJhcHBfaWQiOjE0NTg5fQ&s=a7289ef4bfd80223c3ce7ed7ea964d7d) center / cover'}}>

          <div className='wrapper'>
            <img
              src = {require('./assests/3.jpg')}
              alt = "avatar"
              className ="avatar-img"
            />
            <div className ="homepage-text" >
              <h2>React.js/<span style={{color:'#2bb3c0', fontSize:'35px'}}>UI</span>developer</h2>
              <hr/>
              <h6 style={{color:'#888'}}><span className='homepage-h6-1'>Html5/Css3</span> |<span className='homepage-h6-2'> Bootstrap</span> | <span className='homepage-h6-3'style={{color:'orangered'}}>JavaScript </span>| <span className='homepage-h6-4'> Java </span>| <span className='homepage-h6-5'> React </span>| <span className='homepage-h6-6'> NodeJs</span> </h6>
              <p> Web/UI Developer with 5+ years hands on experience in designing and developing user friendly web sites, web application, mobile application, application architecture, game Development, analysis and implementation of complex software systems with a board range of programming languages and platforms. Experienced in testing, debugging and maintaining the codes. Certified in advance programing language and variety of trending front end developing tools and technologies.
              <span style={{color:'#2bb3c0'}}>Authorized to work in US for any employer</span>.</p>
            </div>
            <Socialmedia />
          </div>
        </div>
      </div>
    );
  }
}
export default Home;



