import React,{Component} from 'react';
import Main from './main';
import Bottom from './bottom';
import {Content} from 'react-mdl';
import './App.css';

class App extends Component{
  render(){
    return(
      <Content>
        <Main />
        <Bottom />
      </Content>
    );
  }
}
export default App;



